import { stringify } from "query-string";
import http from "./http";
/**
 * 获取产品列表
 * @param probability {string} 类型 ： 全部=>ALL
 * @param amountEnum {string} 类型 ： 全部=>ALL
 * @param amountEnum {number}
 * @param pageSize {number}
 */
export const queryProduct = data =>
  http.post(`/product/queryProduct?${stringify(data)}`);

/**
 * 实名接口
 */
export const attest = data => http.post(`/user/attest?${stringify(data)}`);
/**
 * 获取短信验证码
 */
export const smsSend = data =>
         http.post('/sms/smsSend', data);