<template>
  <div
    @click="startCount"
    class="number-count"
  >
    <span class="color-theme font-alternate">{{ countMsg }}</span>
  </div>
</template>

<script>
// 倒计时组件
export default {
  name: "number-count",
  data() {
    return {
      timer: null,
      count: 60,
      first: true,
      disabled: false
    };
  },
  computed: {
    countMsg() {
      let msg = "发送";
      console.log(this.count, "count");
      if (this.count == 60) {
        msg = this.first ? "发送" : "重新发送";
      } else {
        msg = `${this.count}s`;
      }
      return msg;
    }
  },
  methods: {
    startCount() {
      if (this.count === 60 && !this.disabled) {
        this.$emit("onClick");
        this.disabled = true;
      }
    },
    // 开始倒计时
    $startCount() {
      this.timer = setInterval(() => {
        if (this.count > 0) {
          this.count--;
        } else {
          this.$resetCount();
        }
        this.first = false;
      }, 1000);
    },
    // 重置倒计时
    $resetCount() {
      clearTimeout(this.timer);
      this.count = 60;
      this.disabled = false;
    }
  },
  created() {}
};
</script>
<style lang="stylus" scoped>
.number-count
  width 190px
  height 44px
  display flex
  align-items center
  justify-content center
  border-left 1px solid #beb9d0

</style>
