        <p class="color-white">快速审核，极速放款</p>
        <div class="home-top-bottom flex">
          <div class="flex-item">
            <p class="max-loan-title color-white">最高可借(元)</p>
            <p class="load-amount color-white bold font-alternate">100,000</p>
          </div>
          <a
