          >立即申请</a>
        </div>
      </div>
      <div
        class="home-banner"
        v-if="banner.length"
      >
        <md-swiper
          ref="swiper"
          :is-prevent="false"
          :useNative-driver="false"
        >
          <md-swiper-item
            :key="$index"
            v-for="(item, $index) in banner"
          >
            <img
              :src="item"
              alt=""
            />
          </md-swiper-item>
        </md-swiper>
      </div>
      <list-title
        @rightClick="routerList"
        class="mg-t-20 mg-b-20"
      ></list-title>
      <div class="goods-list mg-l-20 mg-r-20">
        <div
          v-for="item in [1, 2, 3]"
          :key="item"
          class="goods-item-panel"
        >
          <GoodsItem />
        </div>
        <div
          @click="routerList"
          class="more-goods-link color-desc flex"
        >
          <span>查看更多产品</span>
          <md-icon
            name="arrow-right"
            size="xs"
          ></md-icon>
        </div>
      </div>
      <!--  检测被拒原因  -->
