<script>
import {
  Swiper,
  SwiperItem,
  Icon,
  InputItem,
  Field,
  ScrollView,
  ScrollViewRefresh,
  Agree
} from "mand-mobile";
import schema from "async-validator";
import GoodsItem from "@/components/GoodsItem";
import Title from "@/components/Title";
import CountDown from "@/components/CountDown";

import { queryProduct, smsSend } from "@/api/";
import { reg } from "@/config/";

export default {
  name: "home",
  components: {
    [GoodsItem.name]: GoodsItem,
    [Title.name]: Title,
    [CountDown.name]: CountDown,
    [Icon.name]: Icon,
    [InputItem.name]: InputItem,
    [Field.name]: Field,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewRefresh.name]: ScrollViewRefresh,
    [Agree.name]: Agree
  },
  data() {
    return {
      banner: [
        require("../../assets/images/banner.png"),
        require("../../assets/images/banner.png"),
        require("../../assets/images/banner.png")
      ],
      form: {
        name: "",
        idCard: "",
        mobile: "",
        verifyCode: "",
        isAgree: true
      },
      formRules: {
        name: [
          {
            type: "string",
            required: true,
            message: "姓名不能为空"
          },
          {
            pattern: reg.name,
            message: "你输入的姓名不合法,请重新输入"
          }
        ],
        idCard: [
          {
            type: "string",
            required: true,
            message: "身份证号不能为空"
          },
          {
            pattern: reg.idCard,
            message: "你输入的身份证号不合法,请重新输入"
          }
        ],
        mobile: [
          {
            type: "string",
            required: true,
            message: "手机号不能为空"
          },
          {
            pattern: reg.mobile,
            message: "你输入的手机号不合法,请重新输入"
          }
        ],
        verifyCode: [
          {
            type: "string",
            required: true,
            message: "短信验证码不能为空"
          },
          {
            pattern: /\d{4}/,
            message: "你输入的验证码不合法,请重新输入"
          }
        ],
        isAgree: [
          {
            type: "boolean",
            required: true,
            validator(rule, value) {
              return value;
            },
            message: "你没有同意注册协议和信用检测协议"
          }
        ]
      }
    };
  },
  methods: {
    $_onRefresh() {
      // async data
      setTimeout(() => {
        this.$refs.scrollView.finishRefresh();
      }, 2000);
    },
    routerList() {
      window.location.href = "list.html";
    },
    getMsg() {
      if (!this.form.mobile) {
        return this.$toast.info("手机号不能为空");
      }
      if (!reg.mobile.test(this.form.mobile)) {
        return this.$toast.info("手机号格式不正确");
      }
      return smsSend({
        mobile: this.form.mobile,
        type: "1"
      }).then(() => {
        this.$refs.msgBox.$startCount();
      });
    },
    checkPersonInfo() {
      // validate form
      console.log(this.form, "form");
      const that = this;
      this.validator.validate(this.form, (errors) => {
        if (errors) {
          console.log(errors, "errors");
          that.$toast.info(errors[0].message);
        } else {
          // api
          // res handler
        }
      });
    },
    // 顶部卡片 申请
    applyLoan() {
      // 自营实名认证
      // 认证授信失败 跳转【授信失败推荐产品】
      // window.Android.getPostMessage("aaaaaaaa");
      window.location.href = "https://webaifei.github.io/loan-remote/list.html";
      // // 未认证 跳转实名认证
      // this.$router.push("realname");
    },
    queryProduct() {
      const data = {
        pageSize: 20,
        pageNum: 1,
        probability: "ALL",
        amountEnum: "ALL"
      };
      return queryProduct(data).then(res => {
        console.log(res);
      });
    }
  },
  created() {
    
    setTimeout(()=> {
      this.$refs.scrollView.finishRefresh();
    }, 100);
    this.validator = new schema(this.formRules);
    this.queryProduct();
    // Toaster.postMessage("created");
  }
};
</script>
